
@import "../../../assets/css/normalize.css";
@import "../../../assets/css/pocketgrid.css";
html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
@font-face {
  font-family: 'Segment7Standard';
  src: url('../../../../static/fonts/Segment7Standard.otf') format('opentype');
  font-weight: normal;
  font-style: italic;
}
html {
  background-color: slategray;
}
#app {
  width: 1024px;
  margin: 0 auto;
}
.warning-strip {
  background: repeating-linear-gradient(-45deg, #bfa50c, #bfa50c 15px, #2d2d2d 15px, #2d2d2d 31px), url(../../../../static/img/grunge_overlay.png) center repeat;
  background-blend-mode: multiply;
}
.danger-strip {
  background: repeating-linear-gradient(-45deg, #ee321b, #ee321b 15px, #390a04 15px, #390a04 31px), url(../../../../static/img/grunge_overlay.png) center repeat;
  background-blend-mode: multiply;
}
html {
  background: url(../../../../static/img/grunge_overlay.png) repeat, #2f3440;
  background-blend-mode: multiply;
}
.content {
  padding: 20px;
  background: #2f3440;
  color: #ebebeb;
}
nav {
  padding: 5px;
  background: repeating-linear-gradient(-45deg, #bfa50c, #bfa50c 15px, #2d2d2d 15px, #2d2d2d 31px), url(../../../../static/img/grunge_overlay.png) center repeat;
  background-blend-mode: multiply;
}
nav .nav-item-wrapper {
  padding: 2px;
}
nav .nav-item-wrapper,
nav .nav-item-wrapper:focus {
  outline: none;
}
nav .nav-item {
  display: block;
  padding: 10px;
  border-radius: 6px;
  color: #ebebeb;
  background-color: #191c23;
  text-decoration: none;
  text-align: center;
}
nav .nav-item,
nav .nav-item:focus {
  outline: none;
}
nav .nav-item.router-link-exact-active {
  color: #ebebeb;
  background-color: #454c5d;
}



